import React, { useRef, useEffect, useCallback, useState} from 'react';
import UnknownProgressBar from 'components/progressbar/UnknownProgressBar.js';
import {connect} from 'react-redux';
import {useScroll} from 'components/hooks/useScroll.js';
import {KEYON_SONGS} from 'components/nft-jacker/History.js';
import './chart.scss';

// switch to true/false to enable charts as a feature
window.CHARTS_FLAG = true;

const SEARCH_TIME_OUT = 1000;

const Chart = ({searchTerm, jacked, loadJack, history=[], showChart}) => { 
    let [loading, setLoading] = useState(false);
    let [chartData, setChartData] = useState([]);
    let [keyonSongs, setKeyonSongs] = useState([]);
    let [selectedTab, setSelectedTab] = useState("history");
    let ref = useRef();
    let searchId = useRef(0);

    // size is a variable telling us how many items to show in the list
    // it updates as we scroll
    let {size} = useScroll(
        ref,
        Math.ceil((window.innerWidth / 200) * (window.innerHeight / 200)), // intial # to load
        searchTerm);

    useEffect(() => {
        if (searchTerm !== "") {
            setSelectedTab("new");
        }
    }, [searchTerm, setSelectedTab]);

    useEffect(() => {
        if (!window.CHARTS_FLAG) {
            return;
        }

        KEYON_SONGS().then(x => setKeyonSongs(x));

        if (selectedTab == "history") {
            // set to history data
            setChartData(history);
        } else if (selectedTab == "spores") {
            // set to keyon songs
            setChartData(keyonSongs);
        } else if (selectedTab == "new") {
            // set to chart/new data
            let baseURL = window.location.hostname === "localhost" ? "http://localhost:9090/charts" :  "https://zequencer.io/charts";
            if (!window.LOCAL_BACKEND_FLAG) {
                baseURL = "https://zequencer.io/charts";
            }
            if (searchTerm && searchTerm !== "") {
                let id = ++searchId.current;
                setTimeout(() => {
                    if (id !== searchId.current) {
                        return;
                    }
                    setLoading(true);
                    fetch(baseURL + "/"+searchTerm).then(async x => {
                        let json = await x.json();
                        setLoading(false);
                        setChartData(json);
                    });
                }, SEARCH_TIME_OUT);
            } else {
                setLoading(true);
                fetch(baseURL).then(async x => {
                    let json = await x.json();
                    setLoading(false);
                    setChartData(json);
                });
            }
        }
    }, [history, setChartData, searchTerm, jacked, selectedTab]);

    return (<>
        <div className='tab-container' style={{display: showChart ? "flex" : "none"}}>
            <div
            className={selectedTab == "spores" ? "tab pushable selected" : "tab pushable not-selected"}
            onClick={() => setSelectedTab("spores")}>Spores</div>
            <div className={selectedTab == "history" ? "tab pushable selected" : "tab pushable not-selected"}
            onClick={() => setSelectedTab("history")}>History</div>
            <div className={selectedTab == "new" ? "tab pushable selected" : "tab pushable not-selected"}
            onClick={() => setSelectedTab("new")}>Newly Minted</div>
        </div>
        <div
          ref={ref}
          className="music-chart" style={{display: showChart ? "flex" : "none"}}>
          {loading ? <UnknownProgressBar width={140} text="Searching the metaverse"/> : chartData.slice(0, size).map(x =>
              <div
                className="chart-item">
              <SongInfo loadJack={loadJack} data={x.jack ? {... x, token: {
                  ... x,
                  metadata: x.jack
              }} :  x}/>
            </div>)}
        </div></>);
};

export const SongInfo = ({data, loadJack, textColor}) => {

    let artist = '';
    let song = '';
    let coverArt = '';
    let externalLink = '';
    let collectionName = '';

    if (data.isSporeTrack) {
        // spore song
        artist = data.metadata.artist;
        song = data.metadata.name;
        coverArt = data.metadata.image;
        externalLink = data.metadata.link;
        collectionName = "Spore Season 1: Genesis";
    } else {
        // mintsongs & catalog
        if (data.token.collectionName != "Zora") {
            artist = data.token.metadata.artist;
            song = data.token.metadata.title || data.token.metadata.name;
            coverArt = "" + data.token.metadata.image;
            externalLink = data.token.metadata.external_url;
        } else {
        // zora
            song = data.token.name;
            externalLink = "https://zora.co/collections/zora/" + data.token.tokenId;
            coverArt = "/zora-placeholder.png";
        }

        collectionName = data.token.collectionName;
        
        if (!coverArt.includes("zora-placeholder")) {
            if (coverArt.includes("ipfs.io")) {
            } else if (coverArt.startsWith("ipfs://")) {
                coverArt = "https://zora-prod.mypinata.cloud/ipfs/" + coverArt.slice(7);
            }
        }
    }


    const onClick = useCallback(() => {
        let ret = {};
        if (data.isSporeTrack) {
            ret = { ... data.metadata};
        } else {
            if  (data.jack) {
                loadJack(data.jack);
                return;
            } else {
                ret = {... data.token.metadata };
                ret.url = data.token.content.url;
            }
        }

        ret.link = externalLink;

        loadJack(ret);
    }, [data, loadJack, externalLink]);
            
    return (<>
              <div
                onClick={onClick}
                className="song-info-container">
            <img className="cover-image pushable" loading="lazy" src={coverArt}></img>
            <div className="song-info">
                <div className="song">{song}</div>
                <div className="artist">{artist}</div>
                <div className="from-text"><a href={externalLink}>{collectionName}↗</a></div>
            </div>
        </div>
        
    </>);
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Chart);
